/* global ajaxurl, wpcotesthemeNUX */
(function (wp, $) {
  'use strict';

  if (!wp) {
    return;
  }

  /*
   * Ajax request that will hide the wpcotestheme NUX admin notice or message.
   */
  function dismissNux() {
    $.ajax({
      type: 'POST',
      url: ajaxurl,
      data: {
        nonce: wpcotesthemeNUX.nonce,
        action: 'wpcotestheme_dismiss_notice',
      },
      dataType: 'json',
    });
  }

  $(function () {
    // Dismiss notice
    $(document).on('click', '.sf-notice-nux .notice-dismiss', function () {
      dismissNux();
    });

    // Dismiss notice inside theme page.
    $(document).on('click', '.sf-nux-dismiss-button', function () {
      dismissNux();
      $('.wpcotestheme-intro-setup').hide();
      $('.wpcotestheme-intro-message').fadeIn('slow');
    });
  });
})(window.wp, jQuery);
